.ripl2 {
    position: relative;
    background-color:#795548 !important;
    border: none;
    /*font-size: 28px;*/
    color: #FFFFFF;
    padding: 20px;
    width: 200px;
    text-align: center; /* Safari */
    transition-duration: 0.4s;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
}
.ripl2:after {
    content: "";
    background: rgba(245, 245, 245, 0.48);
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px!important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
}
.ripl2:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
}






